import React, { Component,Fragment } from 'react'
import {Tree} from 'antd'
const { TreeNode } = Tree;
const searchListMap = [];
//将树形结构转为key，value 一级数据
const generateList = data => {
    for (let i = 0; i < data.length; i++) {
      const node = data[i];
      searchListMap.push({ key:node.value, name: node.name });
      if (node.children) {
        generateList(node.children);
      }
    }
};
//根据当前value，获取树的所有parentkey
const getParentKey = (key, tree) => {
    let parentKey;
    for (let i = 0; i < tree.length; i++) {
      const node = tree[i];
      if (node.children) {
        if (node.children.some(item => item.value === key)) {
          parentKey = node.value;
        } else if (getParentKey(key, node.children)) {
          parentKey = getParentKey(key, node.children);
        }
      }
    }
    return parentKey;
};
export default class Left extends Component {
    constructor(props){
        super(props);
        this.state = {
            expandedKeys:[],
            selectedKeys:[],
            searchValue:"",
            autoExpandParent:false,
            departId:"",
            totalForm:0,
        }
    }

    componentDidUpdate(preProps){
        if(JSON.stringify(preProps.treeData) !== JSON.stringify(this.props.treeData)){
            // if(this.props.userRange==="1"){//本部门 展开一级
            //     this.handleTreeAutoExpand(this.props.treeData);
            // }else{
            //     this.setState({expandedKeys:[]})
            // }
            this.setState({expandedKeys:[]})
            generateList(this.props.treeData);
            let totalForm = this.getTotal(this.props.treeData,0);
            this.setState({totalForm})
        }
        if(JSON.stringify(preProps.searchDepartId)!==JSON.stringify(this.props.searchDepartId)){
            this.setState({
                expandedKeys:Array.from(new Set([...this.state.expandedKeys,this.props.searchDepartId]))
            })
        }
    }
    getTotal = (data,num)=>{
        data.forEach(item=>{
            if(item.type==="user"){
                num = num+1;
            }else{
                num = this.getTotal(item.children,num)
            }
        });
        return num;
    }
    _changeType = (e) => {
        if(e.target.value==="0"){
            this.props.updateTreeData("")
        }else{
            let departId = JSON.parse(window.localStorage.getItem("leftMenuList")).departId
            this.props.updateTreeData(departId)
        }
    }
    renderTreeNodes = (data) => {
        return data.map((item) => {
            let title = item.type==="user"?<span>{item.name} ({item.userCode})</span>:<span>{item.name}</span>;

            if (item.children) {
                return (
                    <TreeNode title={title} key={item.value} isLeaf={item.type==="user"?true:false} dataRef={item}>
                        {this.renderTreeNodes(item.children)}
                    </TreeNode>
                );
            }
            return <TreeNode title={title} key={item.value} isLeaf={item.type==="user"?true:false} dataRef={item}/>;
        });
    }
   // 处理默认自动展开
    handleTreeAutoExpand=(data)=>{
        let keys = data.map(item => item.value);
        this.setState({
            expandedKeys:keys
        })
    }
    callbackExpand=(data,res)=>{
        data.forEach(item=>{
            if(item.children){
                res.push(item.value)
                this.callbackExpand(item.children,res)
            }
        })
    }
    handleCheck=(checkedKeys,e)=>{
        this.setState({totalForm:0})
        const checkedNodes = e.checkedNodes.map(
            item => ({userId:item.props.dataRef.value,name:item.props.dataRef.name,type:item.props.dataRef.type,userCode:item.props.dataRef.userCode,groupUserId:item.props.dataRef.userId})
        )
        this.props.handleCheckTree(checkedKeys,e.halfCheckedKeys,checkedNodes)
    }
    handleExpand=(expandedKeys)=>{
        this.setState({totalForm:0})
        this.setState({
            expandedKeys,
            autoExpandParent:false
        })
    }
    //搜索条件变更
    _onSearch = (searchValue) => {
        const expandedKeys = searchListMap.map(item=>{
            if(item.name.indexOf(searchValue)>-1 || item.key.indexOf(searchValue)>-1){
                return getParentKey(item.key,this.props.treeData)
            }
            return null
        }).filter((item,i,self)=>item&&self.indexOf(item)===i);
        this.setState({
            searchValue,
            expandedKeys,
            autoExpandParent:true
        })
    }
    _filterTreeNode = (node) => {
        const dataRef = node.props.dataRef;
        return this.state.searchValue!==""&&(dataRef.name.indexOf(this.state.searchValue)>-1||dataRef.value.indexOf(this.state.searchValue)>-1)
    }
    render() {
        let treeData=this.props.treeData || []
        if(treeData.length>0){
            return (
                <Fragment>
                    <Tree
                        checkable={true}
                        onCheck={this.handleCheck}
                        onExpand={this.handleExpand}
                        expandedKeys={this.state.expandedKeys}
                        checkedKeys={this.props.selectedKeys}
                        filterTreeNode={this._filterTreeNode}
                    >
                        {
                            this.renderTreeNodes(treeData)
                        }
                    </Tree>
                    <div> 共{this.state.totalForm}人 </div>
                </Fragment>
            )
        }else{
            return null
        }
        
    }
}
