import React, { Component } from 'react';
import {Tree,Button,message,Radio,Select} from 'antd';
import PropTypes from 'prop-types';

import {postAction,getAction} from '@/axios';
import styles from './index.module.less';
const { TreeNode } = Tree;

const searchListMap = [];
let timeout;//函数节流用
let currentValue;
//将树形结构转为key，value 一级数据,方便搜索用
const generateList = data => {
    for (let i = 0; i < data.length; i++) {
      const node = data[i];
      searchListMap.push({ key:node.value, name: node.name });
      if (node.children) {
        generateList(node.children);
      }
    }
};

class SelectUserModal extends Component {
    constructor(props){
        super(props);
        this.state = {
            userRange:'1',//选择人员类型，1：本部门人员，0：全校人员
            userList:[],//搜索用，用户列表
            selectedKeys:[],//选择的人员key
            checkedNodes:[],//选择的人员信息
            treeData:[],//树形菜单数据
            selectValue:null,
        }
    }
    componentDidMount(){
        //异步是为了解决先加载值，树还没生成的告警
        this._getTreeDataRequest('depart').then(()=>{
            let checkedNodes = this.props.formUserList.find(item=>item.formId===this.props.formId).users;
            let selectedKeys = checkedNodes.map(item=>item.userId);
            this.setState({selectedKeys,checkedNodes})
        })
    }
    componentDidUpdate(preProps,preState){
        //formid变化，或者两次的选择人员信息不同，则需要更新
        if(preProps.formId!==this.props.formId || JSON.stringify(preProps.formUserList)!==JSON.stringify(this.props.formUserList)){
            let checkedNodes = this.props.formUserList.find(item=>item.formId===this.props.formId).users;
            let selectedKeys = checkedNodes.map(item=>item.userId);
            this.setState({selectedKeys,checkedNodes})
        }
    }
    //获取树形菜单
    _getTreeDataRequest = (type) => {
        let departId = '';
        if(type==='depart'){
            departId = JSON.parse(window.localStorage.getItem("leftMenuList")).departId
        }
        return new Promise((resolve,reject)=>{
            postAction("/ybt/system/setting/getDepartUser",{departId}).then(res => {
                if(res.success){
                    generateList(res.obj)
                    this.setState({
                        treeData:this._transFormData(res.obj)
                    },()=>{
                        resolve()
                    })
                }else{
                    message.error(res.obj);
                    reject()
                }
            })
        }) 
    }
    //将树转换成需要的结构
    _transFormData = (data) => data.map(item => {
        if(item.userId){//人员
            return ({
                type:"user",
                value:item.userId,
                name:item.name,
                userCode:item.userCode,
                children:[]
            });
        }else{//部门
            let children = [...item.subList,...item.departUser];
            return({
                type:"depart",
                value:item.departId,
                name:item.departName,
                children:this._transFormData(children)
            }) 
        }
    })
    //顶部筛选条件和部分----------start
    //修改选择人员类型
    _changeType = (e) => {
        if (e.target.value === "0"){//全校
            getAction('/ybt/planTask/isAccessToSelectAllSchool').then(
                (res) => {
                    if(res.success) {
                       if (res.obj === 0) {
                           message.warn("暂无查看权限！！！", 8)
                           return false
                       } else if (res.obj === 1) {
                            this.setState({
                                userRange:e.target.value,
                                selectedKeys:[],
                                checkedNodes:[],
                            },()=>{
                                if(e.target.value==="0"){
                                    this._getTreeDataRequest("all")
                                }else{
                                    this._getTreeDataRequest("depart")
                                }
                            });
                       }
                    }
                }
            )
        }else{
            this.setState({
                userRange:e.target.value,
                selectedKeys:[],
                checkedNodes:[],
            },()=>{
                if(e.target.value==="0"){
                    this._getTreeDataRequest()
                }else{
                    let departId = JSON.parse(window.localStorage.getItem("leftMenuList")).departId
                    this._getTreeDataRequest(departId)
                }
            });
        }
    }
    //下拉框搜索事件
    _onSearchUser = (value) => {
        if (value) {
            this._onSearch(value, data => this.setState({ userList:data }));
          } else {
            this.setState({ userList: [] });
          }
    }
    //搜索
    _onSearch = (value,callback) => {
        if (timeout) {
            clearTimeout(timeout);
            timeout = null;
        }
        currentValue = value;
        let search = () => {
            postAction("/ybt/system/setting/user/getUserListByName",{keyword:value}).then(res=>{
                if(res.success){
                    if(currentValue===value){
                        let userList = res.obj.departUser.map(item=>({ value:item.userId,name:item.name,userCode:item.userCode}));
                        callback(userList)
                    }
                }else{
                    message.error(res.obj);
                }
            })
        }
        timeout = setTimeout(search, 600);
    }
    //修改下拉框选择项(使左侧选中并添加选中信息列表)
    _onChangeUser = (value,option) => {
        var userIds = [];
        let selectedKeys = Array.from(new Set([...this.state.selectedKeys,value]));
        let checkedNodes = [...this.state.checkedNodes,{type:"user",userId:value,name:option.props.username,userCode:option.props.usercode}];
        checkedNodes = checkedNodes.filter(item=>{
            if(userIds.indexOf(item.userId)===-1){
                userIds.push(item.userId);
                return true;
            }else{
                return false;
            }
        })
        this.setState({
            selectedKeys:selectedKeys,
            checkedNodes:checkedNodes,
            selectValue:value
        })
    }
    //顶部筛选条件和部分----------end

    //树形菜单事件-------------start
    //树形控件渲染
    _renderTreeNodes = (data) => {
        return data.map((item) => {
            let title = item.type==="user"?<span>{item.name} ({item.userCode})</span>:<span>{item.name}</span>;
            if (item.children) {
                return (
                    <TreeNode title={title} key={item.value} isLeaf={item.type==="user"?true:false} dataRef={item}>
                        {this._renderTreeNodes(item.children)}
                    </TreeNode>
                );
            }
            return <TreeNode title={title} key={item.value} isLeaf={item.type==="user"?true:false} dataRef={item}/>;
        });
    }
    //处理左侧树的选中事件
    _handleCheck = (checkedKeys,e) => {
        const checkedNodes = e.checkedNodes.map(
            item => ({userId:item.props.dataRef.value,name:item.props.dataRef.name,type:item.props.dataRef.type,userCode:item.props.dataRef.userCode})
        )
        this.setState({
            selectedKeys:checkedKeys,
            halfCheckedKeys:e.halfCheckedKeys,
            checkedNodes:checkedNodes
        })
    }
     //树形菜单事件-------------end
     //弹窗确定
     _ok = () => {
         this.setState({selectValue:false,userList:[]})
         let users = this.state.checkedNodes.filter(item=>item.type==='user').map(item=>({userName:item.name,userId:item.userId}))
         this.props.onOk(users)
     }
     //弹窗取消
     _cancle = () => {
         this.props.onClose();
         this.setState({selectValue:false,userList:[]})
     }
    render() {
        return (
            <div className={styles.treeContainer}>   
                <div className={styles.top}>
                    请选择要填报的人：
                    <br/>
                    <Radio.Group value={this.state.userRange} onChange={this._changeType}>
                        <Radio value="1">本部门人员</Radio>
                        <Radio value="0">全校人员</Radio>
                    </Radio.Group>
                    <Select
                        showSearch
                        dropdownStyle={{ maxHeight: 400, overflow: 'auto' }}
                        placeholder="请输入姓名或工号搜索"
                        style={{ width: 300 }}
                        filterOption={false}
                        onSearch={this._onSearchUser}
                        onChange={this._onChangeUser}
                        value={this.state.selectValue}
                    >
                        {
                            this.state.userList.map(item=>(<Select.Option username={item.name} usercode={item.userCode} key={item.value} value={item.value}>{item.name}({item.userCode})</Select.Option>))
                        }
                    </Select>
                </div>
                <Tree
                    className={styles.tree}
                    checkable={true}
                    onCheck={this._handleCheck}
                    checkedKeys={this.state.selectedKeys}
                >
                    {
                        this._renderTreeNodes(this.state.treeData)
                    }
                </Tree>
                {/* <div> 共{this.state.totalForm}人 </div> */}
                <div className={styles.bottom}>
                    <Button onClick={this._ok} type='primary'>确 认</Button>
                    <Button onClick={this._cancle}>取 消</Button>
                </div>
            </div>
        )
    }
}

SelectUserModal.propTypes = {
    formUserList:PropTypes.array.isRequired,//选择的人员列表
    formId:PropTypes.string.isRequired,//当前操作的表单id
    onClose:PropTypes.func.isRequired,//关闭弹窗
    onOk:PropTypes.func.isRequired,//确认操作
}

export default SelectUserModal;
