import React from 'react';
import SelectUserAll from './SelectUserAll';//所有填报人填同样表
import SelectUserByForm from './SelectUserByForm';//按表单配置填报人

const Index = () => {
    let taskDistributionType = Number(window.localStorage.getItem('taskDistributionType'))===1?'1':'0';
    return (
        <React.Fragment>
            {
                taskDistributionType === '0'?<SelectUserAll/>:<SelectUserByForm/>
            }
        </React.Fragment>
    )
}

export default Index;
