import React, { Component } from 'react';
import styles from './index.module.less';

export default class Right extends Component{
    _deleteUser = (userId) => {
        this.props.deleteUser(userId)
    }
    
    render(){
        const checkedNodes = this.props.checkedNodes || [];
        return (
            <React.Fragment>
                <div style={{fontSize:16,color:'#232425'}}>请确认已选择的填报人：</div>
                {
                    checkedNodes.filter(item => item.type==="user").map((item,index) => (
                        <div 
                            className={styles.rightDiv} 
                            key={index}
                        > 
                            {item.name} ({item.userCode})
                            <span onClick={()=>this._deleteUser(item.userId)}>X</span>
                        </div>
                    ))
                }
                <div style={{marginTop:"10px"}}>共<span style={{color:"red"}}>{checkedNodes.filter(item => item.type==="user").length}</span>人</div>
            </React.Fragment>
        )
    }
}