import React, { Component } from 'react';
import {Table,Tag} from 'antd';

import styles from './index.module.less';


export default class TableList extends Component {
    constructor(props){
        super(props)
        this.column = [
            {
                title:"表单名称",
                dataIndex:"formName",
                width:"200px"
            },{
                title:"填报人员",
                dataIndex:"users",
                render:(text,record)=>{
                    return text.map(user=>{
                        return <Tag key={user.userId} closable onClose={() => this._delete(user,record)}>
                                    {user.userName}
                                </Tag>
                    })
                }
            }
        ]
    }
    //删除人员
    _delete = (user,record) => {
        this.props.onDelete(user.userId,record.formId)
    }
    render() {
        let dataSource = this.props.formUserList;
        return (
            <div className={styles.right}>
                <Table
                    columns={this.column}
                    dataSource={dataSource}
                    pagination={false}
                    rowKey={(record)=>(record.formId)}
                />
            </div>
        )
    }
}
