/**
 * @description: 根据表单,单独配置选择填报人
 * @param {type} 
 * @return: 
 */
import React, { Component } from 'react';
import {Card,Button,message} from 'antd';
import {withRouter} from 'react-router-dom';
import {getAction,postAction} from '@/axios'

import FormList from './left';
import FormUserTable from './right';

import styles from './index.module.less';


class Index extends Component {
    constructor(props){
        super(props)
        this.state = {
            formList:[],//上一步已选表单列表
            formUserList:[],//需要提交的表单数据
        }
    }
    componentDidMount(){
        let fillPlanInfo = JSON.parse(window.localStorage.getItem("fillPlanInfo"));
        this.setState({
            planTaskId:fillPlanInfo.planTaskId,
        });
        this._getFormList();
    }
    //获取已选表单
    _getFormList = () => {
        let planTaskId = JSON.parse(window.localStorage.getItem("fillPlanInfo")).planTaskId;
        postAction("/ybt/planTask/getPlanForm",{planTaskId}).then(res => {
            if(res.success){
                this.setState({
                    formList:res.obj.map(item=>({formName:item.formName,formId:item.formId}))
                },()=>{this._getFormUserList(planTaskId)})
            }else{
                message.error(res.obj)
            }
        })
    }
    //获取编辑状态下初始数据
    _getFormUserList = (planTaskId) => {
        getAction( `/ybt/planTask/getPlanFormUserList?planTaskId=${planTaskId}`).then(res=>{
            if(res.success){
                //在这里合并formUserList,因为formlist的每一个表单必须都在formUserList里
                let formUserList = this.state.formList.map(form=>{
                    let newForm = res.obj.find(item=>(item.formId===form.formId));//返回结果时候有当前表单
                    if(newForm){
                        return newForm
                    }else{
                        return {...form,users:[]}
                    }
                })
                this.setState({formUserList})
            }else{
                message.error(res.obj);
            }
        })
        // this.setState({
        //     formUserList:[
        //         {formName:"表单一",formId:"001",users:[{userName:"张三",userId:"009"}]},
        //         {formName:"表单二",formId:"003",users:[{userName:"张三",userId:"011"},{userName:"李四",userId:"098"}]},
        //         {formName:"表单三",formId:"002",users:[{userName:"张三",userId:"0032"}]},
        //     ]
        // })
    }
    //右侧删除人员操作
    _deleteUser = (userId,formId) => {
        let formUserList = this.state.formUserList.map(form=>{
            if(form.formId===formId){
                let users = form.users.filter(user=>user.userId!==userId);
                return {...form,users}
            }else{
                return form
            }
        });
        this.setState({formUserList})
    }
    //左侧更新人员操作
    _updateUser = (formId,users) => {
        let formUserList = this.state.formUserList.map(form=>{
            if(form.formId===formId){
                return {...form,users}
            }else{
                return form
            }
        })
        this.setState({formUserList})
    }
    //上一步
    _preStep = () => {
        this.props.history.push('/fillPlan/step2')
    }
    //下一步
    _nextStep = () => {
        let params = this.state.formUserList;
        let ifHasUser = true;
        this.state.formUserList.every(form=>{
            if(!form.users){
                ifHasUser = false;
                return false;
            }else if(form.users.length===0){
                ifHasUser = false;
                return false;
            }else{
                return true;
            }
        })
        if(!ifHasUser){
            message.error("每个表单请至少选择一个填报人");
            return false;
        }
        postAction(`/ybt/planTask/addPlanFormUser?planTaskId=${this.state.planTaskId}`,params).then(res => {
            if(res.success){
                this.props.history.push('/fillPlan/step4')
            }else{
                message.error(res.obj)
            }
        })
    }
    render() {
        return (
            <div className={styles.main}>
                <Card
                    title='新建填报计划'
                    bodyStyle={{padding:'0 24px 24px 24px'}}
                >
                    <div className={styles.container}>
                        <FormList formUserList={this.state.formUserList} updateUser={this._updateUser}/>
                        <FormUserTable onDelete={this._deleteUser} formUserList={this.state.formUserList}/>
                    </div>
                </Card>
                <div className={styles.btns}>
                    <Button onClick={this._preStep} ghost type='primary' style={{marginRight:8}}>上一步</Button>
                    <Button onClick={this._nextStep} type='primary' style={{marginRight:8}}>下一步</Button>
                    <Button onClick={this._cancle}>取消</Button>
                </div>
            </div>
        )
    }
}

export default withRouter(Index);
