import React, { Component } from 'react';
import {message,Button,Modal}from 'antd';
import PropTypes from 'prop-types';
import {postAction} from '@/axios';

import SelectUserModal from './SelectUserModal';
import styles from './index.module.less';

class FormList extends Component {
    constructor(props){
        super(props)
        this.state = {
            formList:[],//选择的表单项,
            usersModal:false,//选择人员弹窗
            formId:"",//当前选择的formId
        }
    }
    componentDidMount(){
        this._getFormList();
    }
    //获取表单列表
    _getFormList = () => {
        let planTaskId = JSON.parse(window.localStorage.getItem("fillPlanInfo")).planTaskId;
        postAction("/ybt/planTask/getPlanForm",{planTaskId}).then(res => {
            if(res.success){
                this.setState({
                    formList:res.obj.map(item=>({formName:item.formName,formId:item.formId}))
                })
            }else{
                message.error(res.obj)
            }
        })
    }
    //打开选择人员弹窗
    _openUserModal = (formId) => {
        this.setState({formId,usersModal:true})
    }
    //关闭选择人员弹窗
    _closeUserModal = () => {
        this.setState({usersModal:false})
    }
    //确认修改人员
    _updateUser = (users) => {
        this.setState({usersModal:false});
        this.props.updateUser(this.state.formId,users);
    }
    render() {
        return (
            <div className={styles.left}>
                {
                    this.state.formList.map(form=>{
                        return (
                            <div key={form.formId} className={styles.formItem}>
                                {form.formName} <Button size='small' onClick={()=>{this._openUserModal(form.formId)}}>选择人员</Button>
                            </div>
                        )
                    })
                }
                <Modal
                    visible={this.state.usersModal}
                    title='选择人员'
                    footer={false}
                    bodyStyle={{height:'60vh',padding:'0 20px'}}
                    onCancel={this._closeUserModal}
                    width='800px'
                >
                    <SelectUserModal 
                        formUserList={this.props.formUserList} 
                        formId={this.state.formId} 
                        onClose={this._closeUserModal}
                        onOk={this._updateUser}
                    />
                </Modal>
            </div>
        )
    }
}

FormList.defaultProps = {
    formUserList:[]
}

FormList.propTypes = {
    formUserList:PropTypes.array.isRequired,//初始化的表单用户关系列表
    updateUser:PropTypes.func.isRequired//更新人员信息,参数(formId,users)
}

export default FormList;
