/**
 * @description: 所有人选择同样表单
 * @param {type} 
 * @return: React.Component
 */
import React, { Component } from 'react'
import styles from './index.module.less'
import {Card,Button,message,Radio,Select} from 'antd'
import Left from './left'
import Right from './right'
import {withRouter} from 'react-router-dom';
import { postAction, getAction } from '@/axios';

let timeout;
let currentValue;
class Step3 extends Component {
    state={
        treeData:[
            // { type:"folder", id:"000", name:"wenjianja", children:[
            //         { type:'form', id:"901", name:"biaodan" },
            //         { type:'folder', id:"008", name:"文件夹二" }
            //     ]
            // }
        ],
        selectedKeys:[],//左侧树勾选的id
        halfCheckedKeys:[],
        checkedNodes:[],//左侧树勾选的树数据
        planTaskId:"",
        userRange:"1",
        selectUser:"",
        userList:[],
        departList:[],//部门搜索列表
        searchDepartId:'',//当前搜索的部门id
    }
    componentDidMount() {
        let fillPlanInfo = JSON.parse(window.localStorage.getItem("fillPlanInfo"));
        // this.getTreeDataRequest();
        this.setState({
            planTaskId:fillPlanInfo.planTaskId,
        })
        this.getInitDataRequest(fillPlanInfo.planTaskId);
    }
    //将树转换成需要的结构
    transFormData = (data) => data.map(item => {
        if(item.userId){//人员
            return ({
                type:"user",
                value:item.userId,
                name:item.name,
                userCode:item.userCode,
                children:[]
            });
        }else{//部门
            let children = [...item.subList,...item.departUser];
            return({
                type:"depart",
                value:item.departId,
                name:item.departName,
                children:this.transFormData(children)
            }) 
        }
    })
    //将分组的数据转换成需要的结构
    transFormGroupData = (data) => data.map(item => {
        if(item.userId){//人员
            return ({
                type:"user",
                value:item.groupUsersId,
                name:item.name,
                userCode:item.userName,
                userId:item.userId,
                children:[]
            });
        }else{//分组名
            return({
                type:"depart",
                value:item.groupId,
                name:item.groupName,
                children:this.transFormGroupData(item.sysUserGroupUsers)
            }) 
        }
    })
    //获取树形目录---按部门或者全校
    getTreeDataRequest = (departId="") => {
        postAction("/ybt/system/setting/getDepartUser",{departId}).then(res => {
            if(res.success){
                this.setState({
                    treeData:this.transFormData(res.obj)
                })
            }else{
                message.error(res.obj);
            }
        })
    }
    //修改选择全校或者本部门或者按分组
    _changeType = (e) => {
        if (e.target.value === "0"){//全校
            getAction('/ybt/planTask/isAccessToSelectAllSchool').then(
                (res) => {
                    if(res.success) {
                       if (res.obj === 0) {
                           message.warn("暂无查看权限！！！", 8)
                           return false
                       } else if (res.obj === 1) {
                            this.setState({
                                userRange:e.target.value,
                                selectedKeys:[],
                                checkedNodes:[],
                            },()=>{
                                this.getTreeDataRequest()
                                // if(e.target.value==="0"){
                                //     this.getTreeDataRequest()
                                // }else{
                                //     let departId = JSON.parse(window.localStorage.getItem("leftMenuList")).departId
                                //     this.getTreeDataRequest(departId)
                                // }
                            });
                       }
                    }
                }
            )
        } else if(e.target.value === "1") {//本部门
            this.setState({
                userRange:e.target.value,
                selectedKeys:[],
                checkedNodes:[],
            },()=>{
                let departId = JSON.parse(window.localStorage.getItem("leftMenuList")).departId
                this.getTreeDataRequest(departId)
                // if(e.target.value==="0"){
                //     this.getTreeDataRequest()
                // }else{
                //     let departId = JSON.parse(window.localStorage.getItem("leftMenuList")).departId
                //     this.getTreeDataRequest(departId)
                // }
            });
        }else{//按分组选择
            this.setState({
                userRange:e.target.value,
                selectedKeys:[],
                checkedNodes:[],
            },()=>{
                //查询分组树
                getAction('/ybt/system/setting/user/getUserGroupList?useType=1').then(res=>{
                    if(res.success){
                        let treeData = this.transFormGroupData(res.obj);
                        this.setState({treeData})
                    }else{
                        message.error(res.obj)
                    }
                })
            })
        }
    }
    //搜索
    _onSearch = (value,callback) => {
        if (timeout) {
            clearTimeout(timeout);
            timeout = null;
        }
        currentValue = value;
        if(this.state.userRange==="2"){
            let search = () => {
                getAction("/ybt/system/setting/user/queryGroupUsers?useType=1&keyword="+value).then(res=>{
                    if(res.success){
                        if(currentValue===value){
                            let userList = res.obj.map(item=>({ value:item.groupUsersId,name:item.name,userCode:item.userName,userId:item.userId}));
                            callback(userList)
                        }
                    }else{
                        message.error(res.obj);
                    }
                })
            }
            timeout = setTimeout(search, 600);
        }else{
            let search = () => {
                postAction("/ybt/system/setting/user/getUserListByName",{keyword:value}).then(res=>{
                    if(res.success){
                        if(currentValue===value){
                            let userList = res.obj.departUser.map(item=>({ value:item.userId,name:item.name,userCode:item.userCode}));
                            callback(userList)
                        }
                    }else{
                        message.error(res.obj);
                    }
                })
            }
            timeout = setTimeout(search, 600);
        }
        
    }
    //编辑状态下获取初始数据---请求
    getInitDataRequest = (planTaskId) => {
        postAction("/ybt/planTask/getPlanUser",{planTaskId}).then(res => {
            if(res.success){
                const  userRange = res.userRange || "1";//如果后端不返回则显示本部门列表
                this.setState({
                    selectedKeys:res.obj.map(item => item.userId),
                    checkedNodes:res.obj.map(item => ({type:"user",...item})),
                    userRange:userRange
                });
                if(userRange ==="1"){//部门
                    let departId = JSON.parse(window.localStorage.getItem("leftMenuList")).departId
                    this.getTreeDataRequest(departId)
                }else{//全校
                    this.getTreeDataRequest();
                }
            }else{
                message.error(res.obj)
            }
        })
    }
    // 处理左侧树的选中事件
    handleCheckTree=(selectedKeys,halfCheckedKeys,checkedNodes)=>{
        this.setState({
            selectedKeys,
            halfCheckedKeys,
            checkedNodes
        })
    }
    //
    _preStep =() => {
        this.props.history.push('/fillPlan/step2')
    }
    //下一步
    _nextStep = () => {
        let isGroup = this.state.userRange==='2'?true:false;
        let users = [];
        if(isGroup){
            let obj = {};
            users = this.state.checkedNodes.filter(item => item.type==="user").map(item=>({userId:item.groupUserId,userName:item.name,userCode:item.userCode})).reduce((cur,next) => {
                if(obj[next.userId]){
                    return cur
                }else{
                    obj[next.userId] = true
                    cur.push(next);
                    return cur
                }
            },[])
        }else{
            users = this.state.checkedNodes.filter(
                item => item.type==="user"
            ).map(item=>({userId:item.userId,userName:item.name,userCode:item.userCode}))
        }
        let params = {
            planTaskId:this.state.planTaskId,
            userRange:this.state.userRange,
            users:users
        }
        if(params.users.length===0){
            message.error("请至少选择一个填报人");
            return false;
        }
        postAction("/ybt/planTask/createPlanUser",params).then(res => {
            if(res.success){
                this.props.history.push('/fillPlan/step4')
            }else{
                message.error(res.obj)
            }
        })
    }
    //
    _cancle = () => {
        this.props.history.push('/fillPlan')
    }
    //修改下拉框选择项(使左侧选中并添加选中信息列表)
    _onChangeUser = (value,option) => {
            var userIds = [];
            let selectedKeys = Array.from(new Set([...this.state.selectedKeys,value]));
            let checkedNodes = [...this.state.checkedNodes,{type:"user",userId:value,name:option.props.username,userCode:option.props.usercode}];
            checkedNodes = checkedNodes.filter(item=>{
                if(userIds.indexOf(item.userId)===-1){
                    userIds.push(item.userId);
                    return true;
                }else{
                    return false;
                }
            })
            this.setState({
                selectedKeys:selectedKeys,
                checkedNodes:checkedNodes,
            })
    }
    //输入框--选择部门时触发
    _onChangeDepart = (value,option) => {
        this.setState({searchDepartId:value});
        //由于不能主动触发tree的check事件获取不到checknodes，所以每次选择后主动去请求数据获取该部门下的人员
        var userIds = [];
        let selectedKeys = Array.from(new Set([...this.state.selectedKeys,value]));  
        let fattenData = (data,users) => data.reduce((users,item) => {
            if(item.userId){//人员
                return users.concat({
                    type:"user",
                    value:item.userId,
                    name:item.name,
                    userCode:item.userCode
            })
            }else{//部门
                let children = [...item.subList,...item.departUser];
                return fattenData(children,users)
            }
        },users);
        postAction("/ybt/system/setting/getDepartUser",{departId:value}).then(res => {
            if(res.success){
                let users = [];
                users = fattenData(res.obj,[]);
                let checkedNodes = [...this.state.checkedNodes,...users.map(item=>({type:'user',userId:item.value,name:item.name,userCode:item.userCode}))];
                checkedNodes = checkedNodes.filter(item=>{
                    if(userIds.indexOf(item.userId)===-1){
                        userIds.push(item.userId);
                        return true;
                    }else{
                        return false;
                    }
                })
                this.setState({
                    selectedKeys:selectedKeys,
                    checkedNodes:checkedNodes,
                })
            }else{
                message.error(res.obj);
            }
        })
    }
    //变化时触发的人员搜索
    _onSearchUser = (value) => {
        if (value) {
            this._onSearch(value, data => this.setState({ userList:data }));
          } else {
            this.setState({ userList: [] });
          }
    }
    //变化时触发的部门搜索
    _onSearchDepart = (value) => {
        if(value){
            this._onSearchDepartCallback(value,data => this.setState({departList:data}))
        }else{
            this.setState({departList:[]})
        }
    }
    //变化时触发的部门搜索回调
    _onSearchDepartCallback = (value,callback)=>{
        if (timeout) {
            clearTimeout(timeout);
            timeout = null;
        }
        currentValue = value;
        let search = () => {
            getAction("ybt/system/setting/depart/getDepartListForQuery?departName="+value).then(res=>{
                if(res.success){
                    if(currentValue===value){
                        let departList = res.obj.map(item=>({ value:item.departId,name:item.departName}));
                        callback(departList)
                    }
                }else{
                    message.error(res.obj);
                }
            })
        }
        timeout = setTimeout(search, 600);
    }
    _deleteUser = (userId) => {
        let checkedNodes = this.state.checkedNodes.filter(item=>item.userId!==userId);
        let selectedKeys = checkedNodes.filter(item => item.type==="user").map(item=>item.userId);
        this.setState({
            selectedKeys,
            checkedNodes
        })
    }
    render() {
        let {treeData,selectedKeys}=this.state
        return (
            <div className={styles.main}>
                <Card title='新建填报计划' bodyStyle={{padding:'0 24px 24px 24px'}}>
                    <div className={styles.container}>
                        <div className={styles.layout}>
                            <div style={{fontSize:16,color:'#232425'}}>
                                请选择要填报的人：
                                <br/>
                                <Radio.Group value={this.state.userRange} onChange={this._changeType}>
                                    <Radio value="1">本部门人员</Radio>
                                    <Radio value="0">全校人员</Radio>
                                    <Radio value="2">自定义分组</Radio>
                                </Radio.Group>
                                <Select
                                    showSearch
                                    // value={""}
                                    dropdownStyle={{ maxHeight: 400, overflow: 'auto' }}
                                    placeholder="请输入姓名或工号搜索"
                                    style={{ width: 240 }}
                                    filterOption={false}
                                    onSearch={this._onSearchUser}
                                    onChange={this._onChangeUser}
                                >
                                    {
                                        this.state.userList.map(item=>(<Select.Option username={item.name} usercode={item.userCode} key={item.value} value={item.value}>{item.name}({item.userCode})</Select.Option>))
                                    }
                                </Select>
                                {
                                    this.state.userRange==='0'?
                                   <Select
                                        showSearch
                                        dropdownStyle={{maxHeight:400,overflow:'auto'}}
                                        placeholder="请输入部门名称"
                                        style={{width:240}}
                                        filterOption={false}
                                        onSearch={this._onSearchDepart}
                                        onChange={this._onChangeDepart}
                                   >
                                       {
                                           this.state.departList.map(item=>(<Select.Option key={item.value} name={item.name} value={item.value}>{item.name}
                                           </Select.Option>))
                                       }
                                   </Select>:null
                                }
                            </div>
                            <Left userRange={this.state.userRange} updateTreeData={this._updateTreeData} selectedKeys={selectedKeys} treeData={treeData} handleCheckTree={this.handleCheckTree} searchDepartId={this.state.searchDepartId}/>
                        </div>
                        <div className={styles.layout}>
                            <Right deleteUser={this._deleteUser} checkedNodes={this.state.checkedNodes}/>
                        </div>
                    </div>
                </Card>
                <div className={styles.btns}>
                    <Button onClick={this._preStep} ghost type='primary' style={{marginRight:8}}>上一步</Button>
                    <Button onClick={this._nextStep} type='primary' style={{marginRight:8}}>下一步</Button>
                    <Button onClick={this._cancle}>取消</Button>
                </div>
            </div>
        )
    }
}
export default withRouter(Step3)
